<template>
    <div id="addBlog" class="container">
        <h2 class="display-5 py-2 text-info">添加博客</h2>
        <form v-if="!submited">
            <div class="theme form-group">
                <label for="theme">主题：</label>
                <input type="text" id="theme" v-model="blog.theme" class="form-control">
            </div>
            <div class="content form-group">
                <label for="content" style="display: block">内容：</label>
                <textarea id="content" v-model="blog.content" class="form-control" rows="6"></textarea>
            </div>
            <div class="mb-2">
                <div class="form-check form-check-inline">
                    <label class="form-check-label">
                        <input type="checkbox" class="form-check-input" v-model="blog.checkitems" value="Vue.js">Vue.js
                    </label>
                </div>
                <div class="form-check form-check-inline">
                    <label class="form-check-label">
                        <input type="checkbox" class="form-check-input" v-model="blog.checkitems" value="Node.js">Node.js
                    </label>
                </div>
                <div class="form-check form-check-inline">
                    <label class="form-check-label">
                        <input type="checkbox" class="form-check-input" v-model="blog.checkitems" value="React.js">React.js
                    </label>
                </div>
                <div class="form-check form-check-inline">
                    <label class="form-check-label">
                        <input type="checkbox" class="form-check-input" value="Angular.js">Angular.js
                    </label>
                </div>
            </div>
            <div class="author form-group mb-4">
                <label>作者:</label>
                <select v-model="blog.author" class="form-control">
                    <option v-for="author in authors" :key="author.index">
                        {{ author }}
                    </option>
                </select>
            </div>
            <button v-on:click.prevent='post' class="btn btn-block btn-primary">添加博客</button>
        </form>

        <div v-if="submited">
            <h3 class="text-success display-5">恭喜你，你的博客已发布成功！</h3>
        </div>

        <div class="preview jumbotron mt-4">
            <p>主题: {{ blog.theme }}</p>
            <p>博客内容：{{ blog.content }}</p>
            <p>博客分类:</p>
            <ul>
                <li v-for="checkitem in blog.checkitems" :key="checkitem.id">
                    {{ checkitem }}
                </li>
            </ul>
            <p>博客作者: {{ blog.author }}</p>
        </div>
        
    </div>
</template>

<script>
export default {
  name: "addBlog",
  data() {
    return {
      blog: {
        theme: "",
        content: "",
        checkitems: [],
        author: ""
      },
      authors: ["zhangsan", "lisi", "wangwu"],
      submited: false
    };
  },
  methods: {
    post() {
      this.$http
        .post("http://jsonplaceholder.typicode.com/posts/", this.blog)
        .then(res => {
          console.log(res);
          this.submited = true;
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
  max-width: 600px;
}
</style>



