<template>
    <div id="single-blog" class="jumbotron">
        <h1 class="text-center">{{ blog.title }}</h1>
        <article>{{ blog.body }}</article>
    </div>    
</template>

<script>
    export default {
        name: 'single-blog',
        data() {
            return {
                id: this.$route.params.id,
                blog: {}
            }
        },
        created() {
            this.$http.get('http://jsonplaceholder.typicode.com/posts/' + this.id)
                .then(data => {
                    this.blog = data.body;
                })
                
        }
    }
</script>

<style scoped>

</style>



