<template>
  <div id="showBlogs" class="container" v-theme:column="'narrow'">
      <h3>博客总览</h3>
      <input type="text" class="form-control mb-2" v-model="search" placeholder="搜索">
      <div class="sibgleBlog jumbotron" v-for="blog in filteredBlogs" :key="blog.id"> 
            <router-link :to="'/blog/' + blog.id">
                <h5 v-rainbow>{{blog.title | toUpperCase}}</h5>
            </router-link>
            <p>{{blog.body | snippet}}</p> 
      </div>
  </div>
</template>

<script>
export default {
  name: "showBlogs",
  data() {
    return {
      blogs: [],
      search: ""
    };
  },
  created() {
    this.$http.get("./../../static/posts.json").then(data => {
      this.blogs = data.body.splice(0, 10);
    });
  },
  computed: {
    filteredBlogs() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search);
      });
    }
  },
  directives: {
    rainbow: {
      bind(el, binding, vnode) {
        el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .slice(2, 8);
      }
    }
  },
  filters: {
    toUpperCase(value) {
      return value.toUpperCase();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* #showBlogs {
  max-width: 800px;
} */
</style>



